import { useAccount, useConnect, useDisconnect } from 'wagmi'
import { InjectedConnector } from 'wagmi/connectors/injected'

export const ConnectWallet = () => {
  const { address } = useAccount()
  const { connect } = useConnect({
    connector: new InjectedConnector(),
  })

  const { disconnect } = useDisconnect()
  

  if (address) {
    let start = address.substring(0, 4);
    let end = address.substring(address.length - 5, address.length);

    return (
      <div>
       <button type='button' onClick={() => disconnect()}>{start}....{end} </button>
      </div>
    )
  } else{
    return <button type='button' onClick={() => connect()}>Connect Wallet</button>
  }
   
 
}