import React, { useEffect, useState } from 'react'

export const CountDown = ({ time }) => {
  const [h, setH] = useState(0)
  const [m, setM] = useState(0)
  const [s, setS] = useState(0)
  let endTime = new Date(time).getTime()
  let timer = null // setCountDown
  useEffect(() => {
    setCountDown(endTime)
    return () => {
      clearInterval(timer)
    }
  }, []) // eslint-disable-line
  const setCountDown = (time) => {
    timer = setInterval(() => {
      let differTime = (time - new Date().getTime()) / 1000

      if (differTime > 0) {
        setH(Math.floor(differTime / 3600))
        setM(Math.floor((differTime % 3600) / 60))
        setS(Math.floor(differTime % 60))
      } else {
        clearInterval(timer) // clearInterval
      }
    }, 1000)
  }
  return (
    <span>
      {h}H: {m}M: {s}S
    </span>
  )
}
